.navbar {

    &__cover {
        display: flex;
        position: relative;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #F7F7F7;
        padding: 0.5rem;
    }
    &__position {
        position: absolute;
        top: 4rem;
        right: 4.5rem;
        z-index: 50;
    }
    &__list {
        list-style: none;
        padding-right: 2rem;
    }
    &__icon {
        cursor: pointer;
    }
    &__text {
        text-decoration: none;
        font-family: 'BentonSans-Medium';
        font-size: 18px;
        line-height: 18px;
        color: #0033AB;
    }
    &__head {
        font-family: 'BentonSans-Medium';
        font-size: 18px;
        line-height: 18px;
        text-transform: capitalize;
        color: rgba(46, 56, 77, 0.8);
    }
    &__arrow {
        padding-left: 0.5rem;
        cursor: pointer;
    }
    &__iconmain {
        padding-left: 2rem;
        display: flex;
    }
    &__place {
        padding-left: 0.3rem;
    }
    &__first {

    }
}