.account {

    &__header {
        font-family: 'BentonSans-Book';
        font-size: 18px;
        line-height: 18px;
        color: #0033AB;
        margin: 1rem auto;
        text-align: justify;
        padding-left: 1rem;
    }
    &__space {
        width: 1.5rem;
        border-radius: 50%;
        margin-right: 0.5rem;
    }
    &__spaces {
        width: 3rem;
        border-radius: 50%;
        position: absolute;
        top: 3rem;
        left: 4rem;
    }
    &__full {
        position: relative;
    }
    &__userid {
        position: absolute;
        top: 8rem;
        left: 3rem;
        font-family: 'BentonSans-Bold';
        font-size: 18px;
        line-height: 18px;
        color: rgba(255, 255, 255, 0.8);
    }
    &__table {
        width: 970px;
        margin: 0 auto;
        background: #FFFFFF;
        padding: 1rem 0 1.5rem 0;
        border: none;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
        border-radius: 12px;
    }
    &__head {
        padding-left: 2rem;
        font-family: 'BentonSans-Book';
        font-size: 12px;
        line-height: 12px;
        text-align: justify;
        text-transform: uppercase;
        color: rgba(10, 34, 64, 0.7);
       
    }
    &__first {
        padding: 1.5rem;
        border-bottom: 1px solid rgba(17, 65, 133, 0.08);
    }
    &__view {
        font-family: 'BentonSans-Medium';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #0033A1;
        cursor: pointer;
    }
    &__data {
        font-family: 'BentonSans-Book';
        font-size: 16px;
        line-height: 16px;
        text-align: center;
        color: rgba(0, 14, 33, 0.7);
        padding: 2rem;
    }
    &__new {
        font-family: 'BentonSans-Medium';
        font-size: 18px;
        line-height: 18px;
        color: rgba(0, 14, 33, 0.7);
    }
    &__range {
        font-family: 'BentonSans-Medium';
        font-size: 14px;
        line-height: 151.4%;
        color: #7C868D;
    }
    &__detail {
        padding: 1.5rem;
        text-align: justify;
        border: none;
        max-width: 180px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    &__unlink {
        background: rgba(222, 230, 252, 0.3);
        border: 1px solid #0033A1;
        box-sizing: border-box;
        border-radius: 4px;
        padding: 0.5rem;
        width: 70px;
        font-family: 'BentonSans-Book';
        font-size: 12px;
        line-height: 12px;
        text-align: center;
        margin: 0 auto;
        cursor: pointer;
        color: #0033A1;

    }
    &__advert {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
        background: #FFFFFF;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.04);
        border-radius: 4px;
        padding: 1.5rem;
        margin-top: 1rem;
    }
    &__til {
        font-family: 'BentonSans-Bold';
        font-size: 20px;
        line-height: 60px;
        letter-spacing: -0.857143px;
        color: #0A2240;
    }
    &__tex {
        font-family: 'BentonSans-Bold';
        font-size: 24px;
        line-height: 16px;
        letter-spacing: 0.8px;
        color:  #0033A1;
        margin-bottom: 1rem;
    }
    &__rem {
        font-family: 'BentonSans-Bold';
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.8px;
        color: #F83A43;
    }
    &__modal {
        visibility: visible; 
        opacity: 1;
        position: fixed; 
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%; 
        height: 100%; 
        overflow: auto; 
        background-color: rgb(0,0,0); 
        background-color: rgba(0,0,0,0.4); 
    }
    &__modalcontent {
        margin: 0 auto; 
        width: 500px;
        height: 270px;
        background: #FFFFFF;
        border-radius: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
    &__modaltext {
        font-family: 'BentonSans-Bold';
        font-size: 20px;
        line-height: 60px;
        letter-spacing: -0.857143px;
        color: #0A2240;
    }
    &__close {
        color: #0A2240;
        float: right;
        margin-left: 2rem;
        padding: 1.2rem;
        text-align: center;
        font-size: 24px;
        font-weight: bold;
        cursor: pointer;
    }
    &__modaldisplay {
        padding: 3rem;
        text-align: center;
    }
    &__yes {
        cursor: pointer;
        font-family: 'BentonSans-Bold';
        font-size: 16px;
        line-height: 12px;
        text-align: justify;
        color: white;
        border: none;
        padding: 1rem 3rem;
        background-color: #0033A1;
        border-radius: 3px;
    }
    &__no {
        cursor: pointer;
        font-family: BentonSans;
        font-size: 16px;
        line-height: 12px;
        text-align: justify;
        color: #0033A1;
        border: none;
        padding: 1rem 3rem;
        background: rgba(0, 51, 161, 0.1);
        border-radius: 3px;
        margin-left: 1rem;
    }
    &__modalcover {
        margin-top: 2rem;
    }
    &__board {
        margin-left: 2rem;
    }

    &__top {
        position: absolute;
        bottom: 1rem;
        right: 4.5rem;
    }
    &__bold {
        position: relative;
    }
}
#account {
    &__blue {
        transform: translate(5px, 5px);
    }
    &__first {
        transform: translate(-7px, 1px);
    }
    &__first {
        padding-left: 2rem;
        font-family:'BentonSans-Medium';
        font-size: 12px;
        line-height: 12px;
        text-align: justify;
        color: rgba(10, 34, 64, 0.7);
        text-transform: capitalize;
       
    }
    &__link {
        font-family: 'BentonSans-Book';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: rgba(0, 51, 171, 0.7);
    }
    &__search {
        background: rgba(0, 51, 161, 0.02);
        border: 1px solid rgba(0, 51, 161, 0.2);
        box-sizing: border-box;
        border-radius: 3px;
        padding: 0.6rem;
        font-family: 'BentonSans-Medium';
        font-size: 14px;
        line-height: 14px;
        color: rgba(10, 34, 64, 0.5);
        outline: none;
    }
   
}
    

tbody tr:nth-child(even){
    background: rgba(247, 250, 255, 0.5);
    border: none;
  }
  