.push {
    padding-left: 230px;

    &__cover {
        display: flex;
        justify-content: space-between;
        padding: 0 1rem;
        margin: 1rem 0;
    }
    &__inputsearch {
        width: 270px;
        background: rgba(224, 231, 255, 0.2);
        border: 1px solid #E0E7FF;
        border-radius: 3px;
        padding: 0.5rem;
        font-family: 'BentonSans-Book';
        font-size: 14px;
        line-height: 14px;
        color: rgba(10, 34, 64, 0.5);
        outline: none;
    }
    &__filter {
        width: 149px;
        background: rgba(224, 231, 255, 0.2);
        border: 1px solid #E0E7FF;
        box-sizing: border-box;
        border-radius: 3px;
        margin-left: 1rem;
        padding: 1rem !important;
        font-family: 'BentonSans-Book';
        font-size: 14px;
        line-height: 18px;
        color: rgba(46, 56, 77, 0.8);
    }
    &__head {
        font-family: 'BentonSans-Medium';
        font-size: 25px;
        line-height: 25px;
        color: rgba(0, 51, 171, 0.85);
    }
    &__search {
        background: rgba(0, 51, 161, 0.02);
        border: 1px solid rgba(0, 51, 161, 0.2);
        box-sizing: border-box;
        border-radius: 3px;
        width: 307px;
        height: 48px;
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        color: rgba(10, 34, 64, 0.5);
        outline: none;
        padding-left: 1rem;
    }
    &__header {
        font-family: 'BentonSans-Medium';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #FFFFFF;
        background: #0033AB;
        border: 2px solid #0033AB;
        border-radius: 4px;
        padding: 0.5rem;
        text-decoration: none;

    }
    &__notify {
        font-family: 'BentonSans-Bold';
        font-size: 25px;
        line-height: 25px;
        color: rgba(0, 51, 171, 0.85);
        cursor: pointer;
    }
    &__coverage {
        background: #FFFFFF;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.04);
        border-radius: 4px;
        text-align: center;
        padding: 1rem 0 3rem 0;
        margin-top: 1rem;
        position: relative;
    }
    // &__form {
    //     padding: 1rem 2rem;
    // }
    &__create {
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 22px;
        text-align: center;
        color: rgba(0, 14, 33, 0.7);
        margin: 2rem 0;
    }
    &__label {
        display: block;
        font-family: 'BentonSans-Book';
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 1.125px;
        text-transform: uppercase;
        color: #0033AB;
        padding-bottom: 0.5rem;
    }
    &__container {
        margin-bottom: 1rem;
        width: 100%;
    }
    &__input {
        background: #F1F5F9;
        border: 1px solid #0033AB;
        box-sizing: border-box;
        border-radius: 5px;
        padding: 1rem;
        width: 35%;
        font-family: 'BentonSans-Book';
        font-size: 16px;
        line-height: 19px;
        color: #0033AB;
        outline: none;
    }
    &__cancel {
        position: absolute;
        top: 2rem;
        right: 2rem;
    }
    &__push {
        background: #0033AB;
        border-radius: 4px;
        border: none;
        padding: 1rem 3rem;
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        margin-right: 1rem;
        color: #FFFFFF;

    }
    &__span {
        font-family: 'BentonSans-Light';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: rgba(10, 34, 64, 0.5);
    }
    &__cancelbtn {
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        color: #0033AB;
        border: none;
        padding: 1rem;
    }
    &__contained {
        width: 88%;
    }
}

#push {

    &__file {
        width: 38%;
        height: auto;
        margin: 0 auto;
    }
    &__notify {
        margin-top: -1rem;
        text-transform: capitalize;
        margin-left: -5rem;
        color: rgba(#0A2240, 0.5);
    }
    &__select {
        padding: 3rem !important;
       
    }
}