.device {

    &__head {
        font-family: 'BentonSans-Bold';
        font-size: 25px;
        line-height: 25px;
        padding-left: 1rem;
        color: rgba(0, 51, 171, 0.85);
        margin-bottom: 1rem;
    }
    &__back {
        font-family: 'BentonSans-Medium';
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.8px;
        color: #0033AB;
        text-transform: uppercase;
        display: inline-block;
        padding-left: 0.3rem;
    }
    &__cover {
        margin-top: 2rem;
        display: flex;
        justify-content: space-between;
    }
    &__table {
        width: 725px;
        margin: 0 auto;
        background: #FFFFFF;
        padding: 1rem 0 1.5rem 0;
        border: none;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
        border-radius: 12px;
    }
    &__image {
        height: 237px;
    }
    &__images {
        height: 237px;
    }
}