.login {
    background-image: url("../../images/Login.png");
    background-repeat: no-repeat;
    background-size: cover;
    width: 100vw;
    height: 100vh;

    &__cover {
        width: 497px;
        height: 471px;
        background: #FFFFFF;
        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.04);
        border-radius: 4px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
    &__image {
        text-align: center;
        margin: 2.5rem 0;
    }
    &__form {
        padding: 1rem 3rem;
    }
    &__formcover {
        margin-bottom: 1rem;
    }
    &__label {
        font-family: 'BentonSans-Bold';
        font-size: 12px;
        font-weight: bold;
        line-height: 16px;
        letter-spacing: 1.125px;
        text-transform: uppercase;
        color: rgba(7, 16, 19, 0.8);
        display: block;
        padding-bottom: 0.5rem;
    }
    &__input {
        width: 100%;
        font-family: 'BentonSans-Bold';
        font-size: 16px;
        line-height: 19px;
        color: #071013;
        background: rgba(224, 231, 255, 0.05);
        border: 1px solid #0B2D8A;
        box-sizing: border-box;
        border-radius: 5px;
        padding: 1rem;
        outline: none;
    }
    &__forgot {
        margin-top: 1rem;
        font-family:'BentonSans-Bold';
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.5px;
        color: #0033AB;
        font-weight: bold;
    }
    &__sign {
        background: #0033AB;
        border-radius: 4px;
        border: none;
        padding: 1rem 2rem;
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        font-weight: bold;
        line-height: 18px;
        text-align: center;
        color: #FFFFFF;
    }
    &__btn {
        text-align: right;
        margin-top: 1rem;
    }
}