.cards {
    &__cover {
       margin: 2rem auto;
       width: 1000px;
    }
    &__header {
        font-family: 'BentonSans-Bold';
        font-size: 26px;
        font-weight: bold;
        line-height: 26px;
        text-align: justify;
        color: rgba(0, 51, 171, 0.85);
    }
    &__cardCover {
        position: relative;
        margin-top: 2rem;
        cursor: pointer;
    }
    &__info {
        position: absolute;
        padding-top: 5rem;
        padding-left: 1rem;
        top: 1rem;
        left: 1rem;
        z-index: 100;
        color: #ffffff;
    }
    &__subinfo {
        position: absolute;
        top: 0.7rem;
        left: 1rem;
        padding-left: 4rem;
        z-index: 100;
        color: #FFFFFF;
        margin: 0 auto;
    }
    &__maintitle {
        padding-top: 0.2rem;
        font-family: 'BentonSans-Bold';
        font-size: 25px;
        line-height: 60px;
        text-align: center;
        letter-spacing: -1.04167px;
        color: #FFFFFF;
    }
    &__mainheader {
        width: 182px;
        height: 80px;
        font-family: 'BentonSans-Bold';
        font-size: 28px;
        line-height: 40px;
        letter-spacing: -0.04px;
        color: #FFFFFF;
    }
    &__maininfo {
        font-family: 'BentonSans-Light';
        font-size: 20px;
        line-height: 140%;
        color: #FFFFFF;
        opacity: 0.8;
        width: 183px;
        height: 56px;
    }
    &__select {
        font-family: 'BentonSans-Medium';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: #FFFFFF;
        margin-top: 2rem;
        text-align: right;
        background: rgba(0, 0, 0, 0.2);
        border-radius: 15px;
        padding: 0.5rem 1rem;
        margin-left: 7rem;
    }
    &__flex {
        width: 1000px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
    }
    &__cards {
        width: 225px;
        height: 329px;
        position: relative;
    }
    &__dots {
        position: absolute;
        top: 1rem;
        right: 1rem;
    }
    &__subheader {
        padding-top: 4rem;
        margin-bottom: -2rem;
        font-family: 'BentonSans-Bold';
        font-size: 15px;
        font-weight: bold;
        line-height: 60px;
        letter-spacing: -0.857143px;
        color: white;
    }
    &__subtext {
        font-family: 'BentonSans-Bold';
        font-size: 28px;
        font-weight: bold;
        line-height: 35px;
        letter-spacing: -1.04167px;
        color: #000E21;
        margin-top: -1rem;
        padding-top: 1rem;
    }

    &__displayadmin {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 300px;
        height: 135px;
        border-bottom: 1px solid rgba(17, 65, 133, 0.08);
        padding: 0.5rem 0.5rem 0 0;
    }
    &__cardbox {
        background: #FFFFFF;
        border-radius: 3px;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
        margin-bottom: 2rem;
        position: relative;
    }
    &__btns {
        padding: 1rem;
        text-align: right;
    }
    &__coverbtns {
        padding: 1rem;
        text-align: left;
    }
    &__name {
        font-family: 'BentonSans-Bold';
        font-size: 16px;
        font-weight: bold;
        line-height: 16px;
        color: rgba(0, 14, 33, 0.8);
    }
    &__little {
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        color: rgba(0, 14, 33, 0.5);
        padding-top: 0.5rem;
    }
    &__remove {
        font-family: 'BentonSans-Bold';
        font-size: 12px;
        line-height: 16px;
        text-align: center;
        letter-spacing: 0.8px;
        color: #F83A43;
        border: none;
        padding-right: 1rem;
    }
    &__edit {
        font-family: 'BentonSans-Bold';
        font-size: 12px;
        line-height: 12px;
        text-align: center;
        color: #0033A1;
        background: rgba(222, 230, 252, 0.3);
        border: 1px solid #0033A1;
        box-sizing: border-box;
        border-radius: 4px;
        padding: 0.5rem 1.5rem;
    }
    &__picture {
        width: 60px;
        height: 60px;
    }
    &__management {
        font-family: 'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        color: rgba(0, 14, 33, 0.7);
        border: none;
    }
    &__logout {
        font-family:'BentonSans-Bold';
        font-size: 14px;
        line-height: 14px;
        color: #F83A43;
        border: none;
        padding-top: 1rem;
    }

}